var that;
export default {
	data() {
		return {
			type: 0,
			show: false,
			list: [],
			print: 0,
			money: 0, 
			info:{},
			price:{}
		};
	},
	methods: {
		c_show() {
			this.show = !this.show;
		},
		select(e) {
			let index = e.currentTarget.dataset.index;
			that.print = e.currentTarget.dataset.index;
			that.c_show();
			that.$forceUpdate();
		},
		// 获取提现金额
		getmoney(e) {
			console.log(e.detail.value);
			that.money = e.detail.value;
		},
		// 全部
		getall() {
			that.money = that.price.price;
		},
		btn_ok() {
			that.txlq();
		},
		// 提现到零钱
		txlq() {
			if (that.money == 0) {
				that.$alert('请输入提现金额');
				return;
			}
			if (that.money > that.price.price) {
				that.$alert('您的余额不足！');
				return;
			} 
			that.$request
				.request({
					url: that.api.anchor_order.cash,
					data: {
						cash_price: that.money 
					},
					method:'post',
					Toast: '正在提交...'
				})
				.then(res => {
					that.$alertok('提现成功！') 
					setTimeout(function() { 
						uni.navigateBack({
							delta: 1
						});
					}, 1000);
				});
			 
		},  
		tpaddbank() {
			uni.navigateTo({
				url: '/pages/mine/addbank1/addbank1'
			});
		}, 
		getinfo() {
			 that.$zax.userinfo().then(res=>{
				 let userinfo=res.user_info
				 that.price = userinfo.anchor; 
			 })
		},
	},
	onLoad: function(option) {
		that = this;
		console.log(that); 
		let userinfo=uni.getStorageSync("userinfo");
		if(userinfo){ 
			that.price = userinfo.anchor; 
		}else{
			that.getinfo();
		} 
	} 
};
